* {
  box-sizing: border-box;
  margin: 0 ;
  padding: 0 ;
  font-family: "Roboto", sans-serif;
}
.header-bg {
  background-image: url("https://imgbly.com/ib/nJg9HePVlV.jpg");
  width: 100%;
  height: 100vh;
  z-index: -1;
  position: absolute;
  top: 0px;
}
.header__block-left {
    max-width: 638px;
    width: 100%;
}
.container2 {
  width: 85%;
  margin: auto;
}
.navbar-items{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 97%;
}
.header__block-right img {

  width: 100%;
}
.header__common-block {
  display: flex;
  justify-content: center;
}
.header__block-left h1{
    font-size: 46px;
    line-height: 46px;
    letter-spacing: 0em;
    
    font-size: 46px;
    font-weight: 400;
    line-height: 46px;
    letter-spacing: 0em;
    

}
.header__btn-left{
border-radius: 82px;
padding: 27px 45px;
background: rgba(241, 85, 37, 1);
border: none;
color: white;

}
.header__btn-left svg{
    left: 10px;
    position: relative;
}
.navbar__2{
    margin-top: 12px;
    border-radius: 75px;
}

.header__common-block{
    padding-top: 70px;
}

.header__left-text{
    padding-top: 30px;
    padding-bottom: 10px;
}
.nav-link{
  font-size: 13px;
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 0em;
  text-align: center;
  
}
.nav__right-btn-block1 p{
  margin-bottom: 0px;

}

.link-black {
  color: rgba(60, 62, 80, 1);
  text-decoration: none;
}
.header__block-right img{
  cursor: pointer;
}
.nav__right-btn{
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items:center;
}
.nav__right-btn-block2 svg{
  

}
.nav__right-btn-block2 button{
border-radius: 34px;
padding: 8px 27px;
border: none;
color: white;
background: rgba(56, 191, 242, 1);


}
.nav__right-btn-block2-mobile{
  display: none;
}
.navbar__2{
  background-color: white;
}

@media screen and (max-width:990px)  {
 .navbar__logo-left{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
 }
 .nav__right-btn-block2{
  display: none;
 }
 .nav__right-btn-block2-mobile{
  display: block;
 }
 .nav__text-mobile{
  padding-top: 30px;
 }

 .navbar__2{
  background-color: transparent;
}
}
@media screen and (max-width:850px)  {
.header__common-block{
  display: block;
  justify-content: center;
  width: 100%;
  text-align: center;
}
.header__block-right img{
  max-width: 350px;
  padding-top: 50px;
  width: 100%;
}
.header__block-left-text-mobile{
  display: none;
}
}
@media screen and (max-width:420px)  {
  .header__block-left h1{
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0em;
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0em;
    

  }
  .header__left-text{
font-size: 12px;
line-height: 18px;
letter-spacing: 0em;

  }
  .header__btn-left{
    padding: 10px 30px;
  }
  .header__block-right img{
    max-width: 200px;
    padding-top: 50px;
    width: 100%;
  }
}