table {
    width: 100%;
    border-collapse: collapse;
}

table, th, td {
    border: none;
    padding: 15px 40px;
    text-align: left;
}

th {
    background: #f9f8fd;
    font-weight: 700;
    color:   rgba(56, 191, 242, 1);
    padding: 36px 61px;
    padding-bottom: 15px;
}

.sect7__tr2{
    background: rgba(243, 243, 243, 1);
    border-radius: 10px !important;
}

.sect7__tr4{
    background: rgba(243, 243, 243, 1);
    border-radius: 10px !important;
}

.sect7__tr6{
    background: rgba(243, 243, 243, 1);
    border-radius: 10px;
}

.sect7__tr8{
    background: rgba(243, 243, 243, 1);
}

.sect7__tr9 td{
    font-size: 24px;
    font-weight: 700;
}

@media screen and (max-width: 600px) {
  .scrollable {
    overflow-x: auto;
  }
  th {
      background: #f9f8fd;
      font-weight: 700;
      color:   rgba(56, 191, 242, 1);
      padding: 0px 61px;
      padding-bottom: 15px;
  }
}

.sect7__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    background: rgba(234, 242, 245, 1);

    color: #fff;
  }
  .sect7__logo{
    width: clamp(70px, 5vw, 91px);

  }
  .sect7__header p {
    color: black;
    font-size: clamp(12px, 5vw, 17px );
    line-height: 22px;
    margin: auto;
    max-width: 600px;
    width: 100%;
  }
  @media screen and (max-width: 990px) {
    .sect7__header{
        display: block;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
    .sect7__logo{
        padding-bottom: 30px;
    }
  }
  