.sect__2-block h1 {
font-size: 46px;
line-height: 46px;
text-align: center;
padding-bottom: 40px;

}
.sect__2-block{
    padding-top: 85px;
}
.sect__2-card{
    max-width: 270px;
    width: 100%;
    display: block;
    align-items: center;
    text-align: center;
    background: rgba(234, 242, 245, 1);
    padding: 17px 15px;
    border-radius: 30px;
}
.sect__2-card h5{
font-size: 16px;
line-height: 19px;
}
.sect__2-card p{
    font-size: 13px;
    line-height: 15px;
    
}
.sect__com-blocks{
justify-content:space-evenly;

}

@media screen and (max-width:820px)  {
    .sect__2-block h1{
font-size: 24px;
line-height: 26px;

    }
}
.sect__2-card{
    margin-bottom: 10px;
}


@media screen and (max-width:520px)  {
       .sect__2-card svg{
        max-width: 40px;
       } 
}



