.section6 h4{
font-size: clamp(22px, 5vw, 46px);
line-height: 46px;
text-align: center;

}
.sect6__cards{
    display: flex;
    justify-content: center;
    align-items: center;
}
.sect6_card1{
    display: flex;
    align-items: center;
}
.sect6_card1 img {
    width: clamp(51px, 3vw,61px);
}
.sect6_card2 p{
font-size: clamp(12px, 3vw, 16px);
line-height: 21px;

}
.sect6_card1 {
    gap: 20px;
}
@media screen and (max-width:990px) {
    .sect6__cards{
        display: block;
        text-align: center;
    }
    .sect6_card1{
        display: block;
    }
    .sect6__cards{
        padding-top: 25px;
    }
    .sect6_card1{
        padding-top: 20px;
    }
    .sect6_card1 p{
        padding-top: 10px;
        width: 40%;
        margin: auto;
    }
    
}