
.btn2 {
    background: rgba(241, 85, 37, 1);

    color: white;
    padding: 25px 50px;
    font-size: 16px;
    margin: 4px 2px;
    border: none;
    border-radius: 82px;
  }
  .sect4__botombtn{
    margin: 0 auto;
    display: flex;
    justify-content: center;
    text-align: center;
  }

  .sect4__botombtn h5{
    padding-top:15px;
    max-width: 214px;
    width: 100%;
font-size: 15px;
font-weight: 600;
line-height: 18px;

  }
  .sect4__card1 h5{
    padding-top: 20px;
  }



  @media screen and (max-width:520px) {
    .sect4__botombtn{
        display: block;
    }
    .sect4__botombtn h5{
        margin: 0 auto;
    }
    .btn2{
        max-width: 80%;
        width: 100%;
    }
    
  }