/* style.scss */

.sect5__blocks1 {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .sect5__card1 {
    background: rgba(234, 242, 245, 1);
  }
  
  .sect5__card2 {
    background: rgba(234, 242, 245, 1);
  }
  
  .sect5__card2 h5 {
    font-size: clamp(20px, 4vw, 32px);
  }
  
  .sect5__card1 img {
    max-width: 271px;
    width: 100%;
    min-width: 205px;
  }
  
  .sect5__card1 {
    width: 401px;
    height: 313px;
    justify-content: center;
    text-align: center;
    padding: 0 auto;
  }
  
  .sect5__blocks1 {
    gap: 29px;
  }
  
  .sect5__card2 p {
    font-size: clamp(12px, 5vw, 16px);
  }
  
  .sect5__card2 {
    max-width: 730px;
    width: 100%;
    padding: 54px 100px;
  }
  
  .sect5__card1,
  .sect5__card2 {
    border-radius: 40px;
  }
  
  @media screen and (max-width: 990px) {
    .sect5__blocks1 {
      display: block;
    }
    
    .sect5__card1 {
      margin: 0 auto;
      margin-bottom: 30px;
    }
    .sect5__card1{
      margin-top: 20px;
    }
    .sect5__card1{
      width: 100%;
    }
  }
  .sect5__card1 img{
    padding-top: 20px;
     
  }
  
  @media screen and (max-width: 550px) {
    .sect5__card1 img {
      max-width: 100% !important;
      width: auto !important;
      min-width: 0 !important;
    }
    
  .sect5__card1 {
    width: auto;
    height: auto;
    justify-content: center;
    text-align: center;
    padding: 0 auto;
  }
  
  .sect5__card2 {
    padding: 30px 30px;
  }
  
  }
  